.cell {
    width: 8.33%;
    height: 100%;
    border: 1px solid rgba(0, 0, 0, 0.432);
    border-right: none;
    position: relative;
}

.time {
    display: block;
    margin-top: 20px;
    margin-left: 5px;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.vertical_divider {
    width: 0.1px;
    height: 100%;
    position: absolute;
    z-index: 0;
    border: 0.1px dashed rgb(153, 143, 143);
    top: 0px;
    left: 50%;
}