.event {
    position: absolute;
    z-index: 10;
    height: inherit;
    border: 1px solid rgb(172, 171, 171);
    box-shadow: 3px 3px 5px rgb(124, 124, 124);
    border-radius: 10px;
    padding: 10px 15px;
    &__delete{
        position: absolute;
        width: 16px;
        right: 10px;
        top: 5px;
        cursor: pointer;
    }
    &__title {
        margin: 0;
        font-size: 15px;
    }
    &__time {
        margin: 0;
        margin-top: 7px;
    }
    &__descr {
        margin: 0;
        margin-top: 10px;
        background-color:rgba(255, 255, 255, 0.3);
        padding: 5px 7px;
        border-radius: 4px;
        width: 110px;
        cursor: pointer;
        &-text {
            position: absolute;
            height: auto;
            min-width: 160px;
            background-color:rgba(255, 255, 255, 0.93);
            border: 1px solid #5c5c5c;
            top: 43px;
            border-radius: 10px;
            padding: 15px;
        }
    }
    &__hidden {
        position: absolute;
        z-index: 100;
        border: 1px solid rgb(172, 171, 171);
        box-shadow: 3px 3px 5px rgb(124, 124, 124);
        border-radius: 10px;
        width: 150px;
        height: 100px;
        padding: 10px 20px;
        &-cross {
            position: absolute;
            width: 20px;
            height: 20px;
            top: 5px;
            right: 5px;
            cursor: pointer;
            &:before, &:after {
                content: "";
                position: absolute;
                width: 3px;
                height: 25px;
                background-color: black;
                z-index: 110;
                border-radius: 3px;
                top: -2px;
                right: 9px;
            }
            &:before {
                transform: rotate(-45deg);
            }
            &:after {
                transform: rotate(45deg);
            }
        }
    }
}

.dots {
    position: absolute;
    display: flex;
    bottom: 10px;
    left: 2 0%;
    width: 20px;
    justify-content: space-between;
    cursor: pointer;
}
.dot {
    width: 5px;
    height: 5px;
    border: 1px solid black;
    border-radius: 5px;
    background-color: black;
}

.sideResizable {
    height: inherit;
    width: 5px;
    top: 0;
    position: absolute;
    right: 0;
    cursor: col-resize;
}